.events {
  text-align: center;
  padding: 40px 20px;
  font-family: 'Poppins', sans-serif;
  background-color: #f8f9fa;
}

.events-heading {
  font-size: 36px;
  font-weight: 700;
  color: #223a60;
  margin-bottom: 20px;
}

.events-content {
  max-width: 900px;
  margin: 0 auto;
}

.tech-events {
  text-align: center;
  margin-bottom: 30px;
}

.tech-events h3 {
  font-size: 22px;
  color: #0078D4;
  margin-bottom: 10px;
}

.tech-events p {
  font-size: 16px;
  color: #555;
  line-height: 1.6;
}

.events-gallery {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 20px;
  justify-content: center;
  padding: 20px 0;
}

.event-card {
  overflow: hidden;
  border-radius: 12px;
  background: white;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease-in-out;
}

.event-card img {
  width: 100%;
  height: 180px;
  object-fit: cover;
  border-radius: 12px;
  transition: transform 0.3s ease-in-out;
}

.event-card:hover {
  transform: scale(1.05);
}

.explore-more {
  display: inline-block;
  margin-top: 20px;
  padding: 12px 24px;
  font-size: 16px;
  font-weight: 600;
  color: white;
  background-color: #0078D4;
  text-decoration: none;
  border-radius: 8px;
  transition: background 0.3s ease-in-out;
}

.explore-more:hover {
  background-color: #005fa3;
}

/* Responsive Design */
@media (max-width: 768px) {
  .events-heading {
    font-size: 28px;
  }
  .tech-events h3 {
    font-size: 20px;
  }
  .tech-events p {
    font-size: 14px;
  }
  .event-card img {
    height: 150px;
  }
}
